import React, { Fragment } from 'react'
import { Link, NavLink } from 'react-router-dom'
import { connect } from 'react-redux' //connect individual components to the redux store

import { logout }  from "../actions/auth"
import Alert from "./Alert"
import PropTypes from 'prop-types'


const Navbar = ({auth:{isAuthenticated, loading}, logout}) =>{
    const authLinks = ( 
    <a href="/#" className="navbar__top__auth__link" onClick={logout}>Logout</a> // onclick, call the logout action
    )
    
    const guestLinks = (
      <Fragment>
        <Link className="navbar__top__auth__link" to="/login">
          Login
        </Link>
        <Link className="navbar__top__auth__link" to="/signup">
          Sign Up
        </Link>
      </Fragment>
    );

    return (
      <Fragment>
        <nav className="navbar">
          <div className="navbar__top">

            <div className="navbar__top__logo">
              {/* LOGO */}
              <Link className="navbar__top__logo__link" to="/">
                RealState
              </Link>
            </div>

            {/* SHOW CERTAIN LINKS DEPENDING ON IF USER IF AUTH */}
            <div className="navbar__top__auth">
              {!loading && (
                <Fragment>{isAuthenticated ? authLinks : guestLinks}</Fragment>
              )}
            </div>
          </div>

            {/* PAGE LINKS */}
          <div className="navbar__bottom">
            <li className="navbar__bottom__item">
              <NavLink className="navbar__bottom__item__link" exact to="/">
                Home
              </NavLink>
            </li>
            <li className="navbar__bottom__item">
              <NavLink className="navbar__bottom__item__link" exact to="/listings">
                Listings
              </NavLink>
            </li>
            <li className="navbar__bottom__item">
              <NavLink className="navbar__bottom__item__link" exact to="/about">
                About
              </NavLink>
            </li>
            <li className="navbar__bottom__item">
              <NavLink className="navbar__bottom__item__link" exact to="/contact">
                Contact
              </NavLink>
            </li>
          </div>

        </nav>
        {/* ALERT POPUP COMPONENT */}
        <Alert />
      </Fragment>
    );
    
}

Navbar.prototype = {
    logout: PropTypes.func.isRequired,
    auth: PropTypes.object.isRequired
}

const mapStateToProps = state => ({
    auth: state.auth 
})
export default connect(mapStateToProps, { logout })(Navbar);
// Caution mapStateToProps should always be the first argument passed to connect because it’s configured to receive that function as its first argument. 